.array-bar{
    width: 2px;
    display: inline-block;
    margin: 0 1px;
    
    
}

.array-container{
    position: absolute;
    margin: 0 10%;
    top: 6%;

}

button{
    background: none;

    border:  none;

    border-radius: 40px;
    margin:  2% 1%;
    height: 40px;
    width: 150px;
    background-color: yellow;

}

button:hover{
    background-color: blue;
    height: 50px;
    border:  none;
}
button:active{
    background-color: green;
    height: 50px;
    border:  none;

}
